import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { MyApp } from './app.component';
import { RecruitedPage } from '../pages/recruited/recruited';
import { RecruitingPage } from '../pages/recruiting/recruiting';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { AuthProvider } from '../providers';
import { ResourceService } from '../api/resource';
import { HttpModule, Http, XHRBackend, RequestOptions } from '@angular/http';

export function provideStorage() {
  return new Storage({});
}

@NgModule({
  declarations: [
    MyApp,
    RecruitedPage,
    RecruitingPage,
    HomePage,
    TabsPage,
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/recruited-list/recruited-list.module#RecruitedListPageModule', name: 'RecruitedListPage', segment: 'recruited-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/recruited/recruited.module#RecruitedPageModule', name: 'RecruitedPage', segment: 'recruited', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/recruiting/recruiting.module#RecruitingPageModule', name: 'RecruitingPage', segment: 'recruiting', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/recruit-create/recruit-create.module#RecruitCreatePageModule', name: 'RecruitCreatePage', segment: 'recruit-create', priority: 'low', defaultHistory: [] }
  ]
}),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    RecruitedPage,
    RecruitingPage,
    HomePage,
    TabsPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AuthProvider,
    ResourceService,
    { provide: Storage, useFactory: provideStorage },
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule {}



